@import "https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@400;700&display=swap";
.darker_font {
  font-family: Darker Grotesque, sans-serif;
}

.input-field {
  border: 1px solid #0000;
}

.input-field:focus {
  border-color: #d3d3d3;
  outline: none;
}

.chat_header {
  background-color: #fff;
  align-items: center;
  height: 4rem;
  padding: .5rem;
  display: flex;
}

.header_image {
  height: 80%;
  margin-right: .5rem;
}

.text_block_outer {
  margin-top: 1rem;
}

.message_author {
  color: gray;
  margin-bottom: .25rem;
  font-size: smaller;
}

.chatbot_logo_click {
  cursor: pointer;
  z-index: 99999;
  width: 80px;
  height: 80px;
  position: fixed;
  bottom: 1.25rem;
  right: 1.25rem;
  overflow: hidden;
}

.chat_area {
  z-index: 999991;
  background-color: #fff;
  border-radius: 1rem;
  width: 400px;
  margin: auto;
  display: none;
  position: fixed;
  bottom: 120px;
  right: 24px;
}

.chat-visible {
  display: block;
}

.text_block_inner {
  text-align: left;
  background-color: #fff;
  border-radius: .5rem;
  max-width: 80%;
  margin-right: 0%;
  padding: .5rem;
  display: inline-block;
  box-shadow: 2px 2px 9px #0003;
}

.header_buttons {
  color: gray;
  cursor: pointer;
  font-size: small;
  position: absolute;
  top: 15px;
  right: 15px;
}

.shadow_field {
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 2px 2px 9px #00000080;
}

.chat_container {
  background-color: #f7f7f7;
  max-width: 100%;
  height: 18rem;
  padding: 1.5rem;
  font-family: Darker Grotesque, sans-serif;
  font-size: 16px;
  overflow-y: auto;
}

@media (width <= 600px) {
  .chat_area {
    width: 95%;
    height: 95%;
    position: fixed;
    inset: 0;
  }

  .chatbot_logo_click {
    width: 60px;
    height: 60px;
  }

  .chat_container {
    height: 100%;
  }

  .inside_chat_parent {
    height: 100%;
    display: flex;
  }

  .shadow_field {
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    display: flex;
  }
}
